    opacity: 1;
  }
}
  @-webkit-keyframes fade-left-in  {
    0%{
      opacity: 0;
      margin-right: 30rem;
      -webkit-transform: rotate(0) translateY(0);
              transform: rotate(0) translateY(0);

    }
    50%{
      opacity: 1;
      margin-right: 15rem;
      -webkit-transform:rotate(180deg) translateY(0.8rem);
              transform:rotate(180deg) translateY(0.8rem);
    }
    100%{
      margin-right: 0rem;
      -webkit-transform: rotate(360deg) translateY(0) ;
              transform: rotate(360deg) translateY(0) ;
    }
  }
  @keyframes fade-left-in  {
    0%{
      opacity: 0;
      margin-right: 30rem;
      -webkit-transform: rotate(0) translateY(0);
              transform: rotate(0) translateY(0);

    }
    50%{
      opacity: 1;
      margin-right: 15rem;
      -webkit-transform:rotate(180deg) translateY(0.8rem);
              transform:rotate(180deg) translateY(0.8rem);
    }
    100%{
      margin-right: 0rem;
      -webkit-transform: rotate(360deg) translateY(0) ;
              transform: rotate(360deg) translateY(0) ;
    }
  }
  @-webkit-keyframes rotaFade{
    0%{
      opacity: 0;
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
     100%{
      opacity: 1;
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  @keyframes rotaFade{
    0%{
      opacity: 0;
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
     100%{
      opacity: 1;
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
