


















































.teamList{
    width:900px;
    margin:0 auto;
    margin-top:50px;
}
.teamList>li{
    display: inline-block;
    width:400px;
    height:200px;
    overflow: hidden;
    margin:20px;
    background-image: -webkit-linear-gradient(left top,#201f1f,white);
    background-image: linear-gradient(left top,#201f1f,white);
}
.teamList img{
    width:200px;
    height:200px;
}
.teamList>li>ul{
    display: inline-block;
    vertical-align: top;
    text-align: center;
    width: 180px;
    height:100%;

    line-height: 50px;
    font-weight: bold;
    padding-top: 50px;
}
.teamList>li:hover{
    background: -webkit-linear-gradient(left top,white,#201f1f);
    background: linear-gradient(left top,white,#201f1f);
}
.teamList>li>ul>li:first-child{
    font-size:40px;
}
.teamList>li:nth-child(3)>img,.teamList>li:nth-child(4)>img{
    float: right;
}



