










.clap_stick{
  width:65%;
  display: inline-block;
  background:url("../../assets/clap_stick.png") no-repeat;
  background-size:100% 100%;
  vertical-align: top;
  position:relative;
}
.clap_stick .intro-bank{
  height:100%;
  margin:250px  40px 0 40px;
  font-size: 25px;

}
.clap_stick .intro-bank>li{
  text-indent: 40px;
  margin-bottom:40px;
  font-weight:bold;
}
.bank-title{
  font-size:27px;
  color:white;
  background:black ;
  position:absolute;
  padding:10px 30px;
  left:42%;
  bottom:-18px;
  font-family: 'fangmiao';
}



