


















































  .works{
    width: 80%;
    height:850px;
    overflow:hidden;
    display: inline-block;
    margin-left:70px;
    margin-top:40px;
    position: relative;
  }
  @media (max-width:1050px){
    .works{
      width: 93%;
    }
  }

  .works>ul{
    width:700px;
    height:100%;
    position: absolute;
    top:0;
    left:0;
  }
  .works ul>li{
    display: inline-block;
    margin:40px;
    text-align: center;
    font-size:20px;
  }
  .works li img{
    width:250px;
    height:152px;
    margin-bottom: 20px;
    cursor:pointer;
  }
  .works li img:hover{
    box-shadow: 2px 3px 2px black;
  }
  .works-person4{
    width:123px;
    float:right;
  }
.works_two-transition{
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}
.works_two-enter,.works_two-leave{
  padding-left:300px;
  opacity: 0;
}
.works_one-transition{
  -webkit-transition: all 0.1s linear;
  transition: all 0.1s linear;
}
.works_one-enter,.works_one-leave{
   padding-left:300px;
 opacity: 0;
}




